import { Container } from 'rsuite';
import { Link } from 'react-router-dom';
import ImgWelcome from '../../../assets/img/welcome.png';

function Succes() {
    return (
        <main>
            <Container className='succes-infos'>
                <h1>Vous voil prt !</h1>
                <p>Vous tes maintenant prt  rejoindre Double Tap. Nous vous souhaitons une agrable aventure, terrassez tout ce que vous pouvez et amusez-vous bien.</p>
                <img src={ImgWelcome} alt='Dofus Rtro, le MMORPG franais dit par Ankama Games.' />
                <Link to="/" className='back-home-btn'>Retour  l'accueil</Link>
            </Container>

        </main>
    )
}

export default Succes;