import { useEffect, useState } from 'react';
import { Container } from 'rsuite';
import { Button, ButtonToolbar } from 'rsuite';
import { Link } from 'react-router-dom';
import Card from '../../UI/Card/Index';

// Import images
import ImgTopHome from '../../../assets/img/img_top_home.png';
import DofusLogo from '../../../assets/img/dofus_emeraude.png';
import IconSword from '../../../assets/img/icon_epee.png';
import IconZaap from '../../../assets/img/icon_zaap.png';
import IconMonster from '../../../assets/img/icon_monster.png';
import IconMap from '../../../assets/img/icon_map.png';
import IconAmalia from '../../../assets/img/icon_amalia.png';

function Home() {

    const [widthScreen, setWidthScreen] = useState(window.innerWidth);

    useEffect(() => {
        const handleResize = () => setWidthScreen(window.innerWidth);
        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    });

    return (
        <main>
            <section className='head-section'>
            <Container>
                    <article>
                    {widthScreen < 767 && (
                        <>
                            <div className='image-bg'></div>
                            <img src={ImgTopHome} alt="L'niripsa le soigneur de dofus, en version fan art. (fanart dofus)" className='img-top-home'/>
                        </>
                        )}
                        <h1 className='title-gradient'>Double Tap</h1>
                        <p className='para-big'>Nouvelle gnration</p>
                        <p className='para-big'>de serveur priv</p>
                        <p className='para-medium'>Prparez-vous  un gameplay qui peut enfin vous satisfaire.</p>
                    </article>

                    <aside className='btn-bar-top'>
                        <Container>
                            <ButtonToolbar>
                                <Button appearance="primary" size="lg" className='btn-home btn-gradient'><Link to={"/play"} className="link-white">Jouer</Link></Button>
                                <Button appearance="default" size="lg" className='btn-home'><Link to={"/news"} className="link-white">Nouveauts</Link></Button>
                                <Button appearance="default" size="lg" className='btn-home'><Link to={"/ladder"} className="link-white">Classement</Link></Button>
                            </ButtonToolbar>
                        </Container>
                    </aside>
                </Container>

                <Container>
                    {widthScreen > 767 && (
                        <>
                            <div className='image-bg'></div>
                            <img src={ImgTopHome} alt="L'niripsa le soigneur de dofus, en version fan art. (fanart dofus)" className='img-top-home'/>
                        </>
                        )}
                    </Container>
            </section>

            <section className='card-section'>
                <Card imgSrc={DofusLogo} imgAlt="Le logo de Dofus, le jeu vido d'Ankama." title="Redcouvrez le jeu" para="Vous tes fan de Dofus et vous connaissez le jeu par coeur. Venez le redcouvrir."/>
                <Card imgSrc={IconSword} imgAlt="Une icne d'pe venant de Dofus." title="Un gameplay fluide" para="Un nouveau gameplay vous y est propos, de nouvelles choses sont  dcouvrir et  dvorer."/>
                <Card imgSrc={IconZaap} imgAlt="Une icne de Zaap venant de Dofus." title="Les dimensions sont ouvertes" para="Partout dans le monde se sont ouvertes des failles menant  des dimensions, seriez-vous les trouver ?"/>
                <Card imgSrc={IconMonster} imgAlt="Une icne d'un monstre venant de Dofus." title="Un bestiaire plus diversifi" para="Vous connaissez dj toutes les stratgies de combat ? Venez vous mesurer  des nouveaux monstres."/>
                <Card imgSrc={IconMap} imgAlt="Une icne de la map venant de Dofus." title="Des nouvelles zones" para="Le Monde des Douzes n'est plus un secret pour vous ? Venez dcouvrir un nouvel environnement."/>
                <Card imgSrc={IconAmalia} imgAlt="Une icne d'Amalia venant de Wakfu" title="Contenu officiel" para="Habitu au contenu officiel ? Arne, mtiers, Gladiatrool, traques etc .. Tout est l !"/>
            </section>
        </main>
    )
}

export default Home;